import { IToggleObjectValue } from '../types'

/**
 *   
 * @param object  ,     false
 * @param value    ,     true
 * @desc    ,    
 */
export const toggleObjectValue = (object: IToggleObjectValue) => {
  return (value: string) => {
    Object.keys(object).forEach((key) => {
      object[key] = false
      if (key === value) {
        object[key] = true
      }
    })

    return object
  }
}
